.title-news {
    font-size: 22px;
    line-height: 27px;
    color: $black;
    text-align: center;
    font-weight: 400;
    margin-top: 70px;
    margin-bottom: 100px;

    @include media($lg) {
        margin-top: 20px;
        margin-bottom: 50px;

    }
}
.news {
    display: flex;
    flex-direction: column;
    margin-left: -30px;
    margin-right: -30px;

    @include media($lg) {

    }
    &__block-1 {
        @include col(12,0);
        position: relative;        
    }
    &__block-main {
        display: flex;
        flex-direction: column;
        align-items: start;
        position: relative;
        background: $grey-bg;
    }
    &__advice {
        font-size: 16px;
        line-height: 19.5px;
        padding: 15px 30px;
        text-transform: uppercase;
        // position: relative;
        background: $white;
        border-radius: 8px;
        margin-top: 20px;
        margin-left: 20px;
    }
    &__img {
        position: absolute;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
    &__title {
        // position: relative;
        font-size: 22px;
        line-height: 27px;
        color: $white;
        margin-top: 40px;
        padding: 0 20px;
    }
    &__text {
        font-size: 18px;
        line-height: 22px;
        color: $bg;
        // position: relative;
        margin-top: 20px;
        padding: 0 20px;
    }
    &__date {
        display: flex;
        // position: relative;
        color: $white;
        margin-top: 40px;
        padding: 0 20px 20px;
    }
    &__clock {
        margin-right: 10px;
    }
    &__time {
        line-height: 19.5px;
        font-size: 16px;
    }













    &__second {
        display: flex;
        @include col(12,0);
        margin-top: 30px;
        @include media($xs) {
            flex-direction: column;
        }


        &-block {
            @include col(6,0);
            position: relative;
            margin-right: 30px;

            @include col-xs(12,0);
            @include media($xs) {
                margin-bottom: 15px;
            }
        }
        &-block:last-of-type {
            margin-right: 0;
        }
        &-img {
            position: absolute;
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
        &-info {
            background: $grey-bg;
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: start;
            position: relative;
        }
        &-advice {
            font-size: 16px;
            line-height: 19.5px;
            padding: 10px 15px;
            text-transform: uppercase;
            background: $white;
            border-radius: 8px;
            margin-top: 20px;
            margin-left: 20px;
        }
        &-title {
            font-size: 22px;
            line-height: 27px;
            color: $white;
            margin-top: 30px;
            padding: 0 20px;
        }
        &-text {
            font-size: 18px;
            line-height: 22px;
            color: $bg;
            margin-top: 20px;
            padding: 0 20px;
        }
        &-date {
            display: flex;
            color: $white;
            margin-top: 20px;
            padding: 0 20px 20px;
        }
        &-clock {
            margin-right: 10px;
        }
        &-time {
            line-height: 19.5px;
            font-size: 16px;
        }
    }


}
.news:last-of-type {
    flex-direction: column-reverse;

    @include media($lg) {
        flex-direction: column;
        margin-left: 0;
        margin-right: 0;
        margin-top: 30px;
    }

}

.news:first-of-type {
    margin-right: 60px;
    @include media($lg) {
        margin-right: 0;
        margin-left: 0;
    }
}

.news:last-of-type .news__second {
    margin-top: 0;
    margin-bottom: 30px;
}

.boss {
    display: flex;

    @include media($lg) {
        display: flex;
        flex-direction: column;
    }
}

.news:last-of-type .news__second {
    @include media($lg){
        margin-top: 30px;
        margin-bottom: 0;
    }
}