* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

body {
    font-family: "Montserrat";
    background: $main;
}

.container {
    @include container();
}

.row {
    @include row();
}

.col-lg-8 {
    @include col-lg(8,15) 
}

::before , ::after {
    font-family: "fontawesome";
}

.icon {
    &-search::before {
        content: '\f002';
        color: $black;
        font-weight: 300;
    }
    &-shop::before {
        content: "\f07a";
        color: $black;
        font-weight: 300;
    }
    &-heart::before {
        content: "\f004";
        color: $black;
        font-weight: 300;
    }
    &-person::before {
        content: "\f406";
        color: $black;
        font-weight: 300;
    }
    &-clock::before {
        content: "\f017";
        color: $white;
        font-weight: 300;
    }
    &-vk::before {
        content: "\f189";
        color: $black;
        font-weight: 300;
    }
    &-youtube::before {
        content: "\f167";
        color: $black;
        font-weight: 300;
    }
    &-instagram::before {
        content: "\f16d";
        color: $black;
        font-weight: 300;
    }
    &-facebook::before {
        content: "\f39e";
        color: $black;
        font-weight: 300;
    }
    &-points::before {
        content: "\f142";
        font-weight: 300;
        color: $black;
        font-size: 40px;
    }

}
