$Montserrat: ("Montserrat" ,"Regular", 400),
             ( "Montserrat" ,"Bold", 700),
             ( "fontawesome" ,"duotone", 900),
             ( "fontawesome" ,"light", 300),
             ( "fontawesome" ,"brands", 300)
             ;
@each $family ,$name, $weight in $Montserrat {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}

$white: #fff;
$black: #000;
$grey: #323232;
$main: #F1F1F1;
$bd: #C4C4C4;
$bg: #E1E1E1;
$grey-bg: linear-gradient(180deg, rgba(50, 50, 50, 0.75) 0%, rgba(50, 50, 50, 0.5) 100%);