.leader {
    padding-left: 0;
    padding-right: 0;
    
    &__main {
        @include row(0);
        justify-content: space-between;
        align-items: center;
    }

    &__title {
        line-height: 26.82px;
        font-size: 22px;
        text-transform: uppercase;
        text-align: center;
        margin: 100px 0;
    }
    &__block {
        @include col(3,0);
        @include col-md(5,0);
        @include col-xs(12);
        margin: 0 -15px;
        background: $bg;
        border-radius: 8px;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        margin-bottom: 30px;

        @include media($xs){
            margin: 0 0 20px 0;
        }
    }
    &__heart {
        position: absolute;
        top: 20px;
        right: 20px;
        padding: 16px;
        background: $white;
        border-radius: 8px;
    }
    &__shop {
        position: absolute;
        top: 85px;
        right: 20px;
        padding: 16px;
        background: $white;
        border-radius: 8px;
    }
    &__sale {
        background: $grey;
        padding: 10px 30px;
        font-size: 20px;
        line-height: 24.38px;
        color: $white;
        border-radius: 8px;
        position: absolute;
        top: 20px;
        left: 20px;
    }
    &__img {
        margin-top: 20px;
        width: 96px;
        height: 185px;
        object-fit: contain;
    }
    &__text {
        font-weight: 700;
        font-size: 20px;
        line-height: 24.38px;
        margin: 27px 0 16px 0;
    }
    &__price {
        font-size: 18px;
        line-height: 21.94px;
        margin-bottom: 23px;
    }
}