.nav {
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid $bd;
    align-items: center;
    position: relative;
    @include media($sm) {
        justify-content: center;
        position: sticky;
        top: 0;
        z-index: 999;
        border-bottom: none;
        padding: 0;
    }

    &__list {
        display: flex;

        @include media($sm) {
            display: none;
            &.active {
                display: flex;
                position: absolute;
                flex-direction: column;
                top: 50px;
                align-items: center;
                z-index: 1;
                background: $grey;
                width: 100%;
                border-radius: 20px;

            }
        }
    }

    &__link {
        margin: 0 25px;
        font-size: 16px;
        color: $grey;
        @include media($sm) {
            display: flex;
            width: 100%;
            margin: 20px 0;
            color: $white;

        }
    }

    &__menu {
        display: none;
        text-transform: uppercase;
        color: $black;
        background: $main;
        @include media($sm) {
            position: sticky;
            top: 0;
        }

        @include media($sm) {
            display: block;
            margin: auto;
            width: 100%;
            text-align: center;
            padding-bottom: 20px;
            padding-top: 20px;
            
        }
    }
    
    &__points {
        padding: 3px 10px;
    }

    &__items {
        display: flex;

        @include media($md){
            position: absolute;
            right: 30px;
            bottom: 15px;
            background: $bg;
            padding: 17px;
            border: 1px solid $black;
            border-radius: 10px;
            visibility: hidden;

            &.active {
                visibility: visible;
                @include media($sm) {
                    visibility: hidden;
                }
            }
        }

    }

    &-icon {
        line-height: 18px;
        font-size: 18px;
        margin: 0 15px;
    }

    .icon-points {
        display: none;  
        
        @include media($md) {
            display: inline-block;
        }
        @include media($sm) {
            display: none;
        }
    }
}
