

.first {
    display: flex;
    justify-content: space-between;    
    margin-top: 40px;
    @include row(15px);
}

.big {
    @include col(8,0);
    @include col-lg(12,0);
}

.content {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    align-items: center;
    background: $bg;
    border-radius: 8px;
    padding: 197px 0 181px 0;
    @include media($xs) {
        padding: 80px 0 50px 0;
        flex-direction: column;
    } 

    &__img {
        @include media($xs) {
            width: 200px;
            height: 300px;
            object-fit: contain;
        }
    }

    &__heart {
        position: absolute;
        right: 27px;
        top: 20px;
        padding: 16px;
        background: $white;
        border-radius: 8px;
    }

    &__shop {
        position: absolute;
        right: 27px;
        top: 85px;
        padding: 16px;
        background: $white;
        border-radius: 8px;
    }

    &__sale {
        padding: 10px 30px;
        background: $grey;
        color: $white;
        border-radius: 8px;
        position: absolute;
        top: 20px;
        left: 13px;
        line-height: 24.38px;
    }

    &__text {
        margin-left: 40px;
        display: flex;
        flex-direction: column;
        align-items: center;

        @include media($xs) {
            margin-left: 0;
        }
    }

    &__title {
        font-family: "Montserrat";
        font-size: 20px;
        font-weight: 700;
        line-height: 24.38px;

    }

    &__price {
        font-family: "Montserrat";
        line-height: 21.94px;
        margin-top: 16px;
        font-size: 18px;
    }
}

.block {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: $bg;
    text-align: center;
    justify-content: center;
    padding: 22px 0 27px 0;

    @include col(12);
    @include col-lg(5);

    &__img {
        @include media($xs){
            width: 70px;
            height: 120px;
            object-fit: contain;
        }
    }

    &__title {
        font-family: "Montserrat";
        font-weight: 700;
        margin-top: 35px;
        line-height: 24.38px;
        font-size: 20px;

        @include media($xs) {
            margin-top: 10px;
            font-size: 13px;
        }
    }

    &__price {
        line-height: 21.94px;
        margin-top: 16px;
        font-family: "Montserrat";
        font-size: 18px;
        
        @include media($xs) {
            margin-top: 0;
            font-size: 10px;
        }

    }

    &__heart {
        position: absolute;
        right: 27px;
        top: 20px;
        padding: 16px;
        background: $white;
        border-radius: 8px;

        @include media($sm) {
            padding: 5px;
            bottom: 5px;
            top: inherit;
            right: 5px;
        }
    }

    &__shop {
        position: absolute;
        right: 27px;
        top: 85px;
        padding: 16px;
        background: $white;
        border-radius: 8px;

        @include media($sm) {
            padding: 5px;
            top: 5px;
            right: 5px;
        }
    }

    &__sale {
        padding: 10px 30px;
        background: $grey;
        color: $white;
        border-radius: 8px;
        position: absolute;
        top: 20px;
        left: 13px;
        line-height: 24.38px;

        @include media($sm) {
            padding: 5px;
            left: 5px;
            top: 5px;
        }
    }
}

.small {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include col(4);
    @include col-lg(12);


    @include media() {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-top: 15px;
        display: flex;
    }
}
