.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 18px 0;
    border-bottom: 1px solid $bd;

    &__lang {
        font-size: 18px;
        line-height: 13px;
        text-transform: uppercase;
        color: $grey;
    }
    &__logo {
        line-height: 20px;
    }
}

.logo {
    font-weight: 700;
    font-size: 32px;
    line-height: 23px;
    color: $grey;
}