.nav {
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #C4C4C4;
  align-items: center;
  position: relative;
}

@media (max-width: 768px) {
  .nav {
    justify-content: center;
    position: sticky;
    top: 0;
    z-index: 999;
    border-bottom: none;
    padding: 0;
  }
}

.nav__list {
  display: flex;
}

@media (max-width: 768px) {
  .nav__list {
    display: none;
