.footer__title {
    text-transform: uppercase;
    color: $black;
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 40px;
    font-weight: 400;
}

.products {
    display: flex;
    flex-direction: column;


    &__link {
        font-size: 18px;
        line-height: 13px;
        margin-bottom: 10px;
        color: $black;
        @include media($md) {
            display: flex;
            margin-bottom: 15px;
        }
    }
    &__list {
        display: flex;
        flex-direction: column;
        @include media($md) {
            align-items: center;
        }
    }
    &__address {
        font-size: 18px;
        line-height: 22px;
        color: $grey;
        margin-bottom: 15px;
        color: $grey;

    }
    &__sites {
        display: flex;
        color: $grey;
        margin-top: 22px;
        @include media($md) {
            justify-content: center;
        }

    }
    &__num {
        font-size: 18px;
        line-height: 22px;
        margin-right: 8px;
        color: $grey;
        @include media($md) {
            font-size: 12px;
        }
    }
    &__numbers {
        display: flex;
        @include media($md) {
            justify-content: center;
        }
    }
    &__email {
        font-size: 18px;
        line-height: 22px;
        margin-top: 15px;
        color: $grey;

    }
    &__online {
        margin-top: 25px;
        line-height: 22px;
        font-size: 18px;
        color: $grey;
        font-weight: 400;
    }
    &__icon {
        margin-right: 14px;
    }
}

.footer {
    &__main {
        @include row(30px);
        display: flex;
        justify-content: space-between;
        padding: 65px 0;

        @include media(1200px) {
            margin-left: 0;
            margin-right: 0;
        } 
        @include media($md) {
            justify-content: space-evenly;
            text-align: center;
        }
        @include media($xs) {
            flex-direction: column;
            align-items: center;
        }
    }

}

.products:last-of-type {
    @include media($md) {
        margin-top: 50px;
        display: flex;

    }
}

.products:nth-of-type(2){
    @include media($xs) {
        margin-top: 50px;
    }
}